<template>
  <div class="home"></div>
</template>

<script>
// @ is an alias to /src
export default {
  name: "Home",
  data: function () {
    return {
      test: "",
    };
  },
  created: function () {
    this.test = this.$route.params.id;
  },
  watch: {
    "$route.params.id": function (id) {
      this.test = id;
    },
  },
};
</script>
